/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *::before, *::after {
  box-sizing: border-box;
  /* outline: 1px solid black; */ }

body {
  font-family: 'Roboto', 'Noto Sans TC', sans-serif;
  line-height: 1.5; }

a {
  text-decoration: none;
  display: block; }

a:hover {
  opacity: 0.6; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 12px; }

.text-white {
  color: #FFFFFF; }

.d-flex {
  display: flex; }

.mb-32 {
  margin-bottom: 32px; }

.mb-24 {
  margin-bottom: 24px; }

.mb-8 {
  margin-bottom: 8px; }

.fw-b {
  font-weight: bold; }

header {
  background-color: #AA0601; }

h2 {
  font-size: 36px; }

h3 {
  font-size: 48px;
  color: #650300;
  text-align: center;
  font-weight: bold;
  margin-bottom: 32px; }

h4 {
  font-size: 48px;
  font-style: italic;
  color: #AA0601;
  font-weight: bold;
  text-align: center; }

h5 {
  margin-bottom: 72px; }

.header-box {
  justify-content: space-between;
  align-items: center; }

.menu a {
  font-size: 20px;
  padding: 25px; }

.banner {
  background-image: url("https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/home-header.png");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #650300;
  text-align: right;
  padding-top: 296px;
  padding-bottom: 296px; }
  .banner .banner-box {
    display: block;
    flex-direction: column;
    align-items: flex-end; }
    .banner .banner-box .banner-subtitle {
      font-size: 64px;
      font-weight: bold; }
    .banner .banner-box .banner a {
      display: inline-block;
      padding: 6px 12px;
      font-size: 20px;
      color: #FFFFFF;
      background-color: #AA0601; }
      .banner .banner-box .banner a:hover {
